import * as React from "react";
import { Dialog, DialogContent } from "@mui/material";
import { ModalDialogprops } from "../../types/types";
import CloseIcon from "@mui/icons-material/Close";
import { useAppDispatch } from "../../assets/hooks";
import { closePopup } from "../../store/Slices/popupSlice";

const CustomModal = ({ open, children,additionalData }: ModalDialogprops<{ width: any,height?:any,show?:any,close?:any}>) => {
  const dispatch = useAppDispatch();
  return (
    <>
      <Dialog
        open={open}
        onClose={()=>additionalData?.close === "none" ? console.log(""):dispatch(closePopup())}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        PaperProps={{
          sx: {
            minWidth: "200px",
            minHeight: "100px",
            height:`${additionalData?.height}`,
            width: {xs:"100%",md:`${additionalData?.width}`},
            // borderRadius: "24px",
            padding: "0",
            maxWidth: "1300px",
            maxHeight:"1300px",
          },
        }}
      >
        {additionalData?.show === "none" ? "":
        <CloseIcon
          onClick={()=>dispatch(closePopup())}
          sx={{
            position: "absolute",
            right: 14,
            top: 14,
            color: "#E94040",
            cursor: "pointer",
            "&:hover":{
              backgroundColor:"#E94040",
              color:"#fff",
              // borderRadius:"50%"
            }
          }}
        /> }
        <DialogContent
          sx={{
            overflowY: "scroll",
            "&::-webkit-scrollbar": {
              width: "12px",
            },
            "&::-webkit-scrollbar-thumb": {
              backgroundColor: "#fff",
              borderRadius: "8px",
            },
            "&::-webkit-scrollbar-track": {
              // backgroundColor: "#fff",
            },
            overflow:"visible",
            padding:{xs:0,sm:"0"}
          }}
        >
          {children}
        </DialogContent>
      </Dialog>
    </>
  );
};

export default CustomModal;
